<template>
    <div id="login">
      <div class="login-bg"><img src="../../static/img/login_bg@3x.png"/></div>
      <div class="user" style="padding-top: 90px">
        <!--<img src="../../static/img/icon_zfb@3x.png"/>-->
        <div class="help-input">
          <img src="../../static/img/icon_sj@3x.png"/>
          <input type="number" placeholder="请输入手机号" v-model="phone">
        </div>

        <div class="help-input">
          <img src="../../static/img/icon_mm@3x.png"/>
          <input type="password" placeholder="请输入密码" v-model="password">
        </div>
       <!-- <div class="help-input">
          <img src="../../static/img/icon_yzm@3x.png"/>
          <input type="text" placeholder="请输入验证码" v-model="inputCode">
          <span @click="getCode" :class="{codeStyle:codeName!='获取验证码'}">{{codeName}}</span>
        </div>-->
      </div>
      <div class="sub" @click="login">登录</div>

      <p style="display: flex;justify-content:space-around">
        <span @click="$router.push({path: '/forget'})">忘记密码</span>
        <span @click="$router.push({path: '/register'})">注册</span>
      </p>


      <!--<div class="dsf">-->
        <!--<span>第三方登录</span>-->
        <!--<div>-->
          <!--<img src="../../static/img/icon_wx@3x.png" />-->
          <!--<img src="../../static/img/icon_zfb@3x.png" />-->
        <!--</div>-->
      <!--</div>-->
    </div>
</template>

<script>
    export default {
      name: "login",
      data(){
          return{
            phone:'',
            password:'',
          }
      },
      methods:{
          login(){
            var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
            if(this.phone==''){
              this.$toast('请输入手机号');
              return false;
            }else if(!reg.test(this.phone)){
              this.$toast('手机格式不正确');
              return false;
            }else if(this.password=="") {
              this.$toast('请输入密码');
              return false;
            }
            clearInterval(this.codeTimes);
            this.codeName='获取验证码';
            let params = {
              mobile: this.phone,
              password: this.password,
            }
            this.$POST('/auth/login',params,true).then(res =>{
              console.log(res);
              if(res.code===200 ){
                this.$router.push('/');
                localStorage.setItem('userInfo', JSON.stringify(res.data))
								localStorage.setItem('tokens',res.data.token)
								localStorage.setItem('PID',res.data.alimm_pid)
              }else {
                this.$toast(res.msg);
              }
            }).catch(res=>{
              this.$toast('连接服务器失败!');
              console.log(res);
            })
          },
      }
    }
</script>

<style scoped>
  .codeStyle{
    color:gray !important;
  }
 .login-bg{
   width: 100%;
 }
 .login-bg>img{
   display: block;
   width: 100%;
 }
  .user{
    width: 80%;
    margin: 0 auto;
    position: relative;
    top: -50px;
  }
 .user>img{
   width:90px;
   height:90px;
   border-radius:90px;
   margin: 0 auto;
   margin-bottom: 15px;
 }
 .help-input{
   text-align: left;
   border-bottom: 1px solid #bfbfbf;
   padding-bottom: 10px;
   margin-bottom:25px;
 }
  .help-input>img{
    display: inline-block;
    width: 20px;
    position: relative;
    top:8px;
    margin-left: 5px;
  }
 .help-input>input{
   width:60%;
   border: 0;
   font-size: 18px;
   margin-left: 5px;
 }
  .sub{
    width: 80%;
    margin: 0 auto;
    border-radius: 30px;
    background: linear-gradient(to right, #fe5741 , #fd6c46,#fc7f4a);
    height: 45px;
    line-height: 45px;
    color: #ffffff;
    font-size: 18px;
    margin-top: -15px;
  }
  .dsf{
    width: 80%;
    margin: 0 auto;
    border-top: 1px solid #bfbfbf;
    margin-top: 50px;
  }
 .dsf>span{
   color: #959595;
   position: relative;
   top: -12px;
   display: block;
   background: #ffffff;
   width:100px;
   margin: 0 auto;
 }
 .dsf img{
   display: inline-block;
   width:55px;
   height:55px;
   border-radius:55px;
 }
 .dsf>div{
   width:40%;
   margin: 0 auto;
 }
 .dsf>div>img:first-child{
   float: left;
 }
 .dsf>div>img:last-child{
   float: right;
 }
 .help-input>span{
   float: right;
   line-height: 50px;
   color: #fe513f;
 }
</style>
<style>
  .help-input{
    text-align: left;
    border-bottom: 1px solid #bfbfbf;
    padding-bottom: 10px;
    margin-bottom:25px;
  }
  .help-input>img{
    display: inline-block;
    width: 20px;
    position: relative;
    top:8px;
    margin-left: 5px;
  }
  .help-input>input{
    width:60%;
    border: 0;
    font-size: 18px;
    margin-left: 5px;
  }
  .sub{
    width: 80%;
    margin: 0 auto;
    border-radius: 30px;
    background: linear-gradient(to right, #fe5741 , #fd6c46,#fc7f4a);
    height: 45px;
    line-height: 45px;
    color: #ffffff;
    font-size: 18px;
    margin-top: -15px;
  }
</style>



